:root {
  /* z-index enum */
  --z-index-popup: 2;
  /* various dimensions */
  --dialog-logo-padding: calc(var(--dialog-padding) * 0.66);
  --dialog-padding: 20px;
  --logo-size: 64px;
  --pauseresume-size: 20px;
  --popup-top: 51px;
  --progressbar-border: 1px;
  --progressbar-height: 20px;
  --toolbar-height: 50px;
  /* colors related to torrent status */
  --black: #000;
  --blue-100: #51b3f7;
  --blue-200: #357aaa;
  --blue-300: #2c7fea;
  --blue-400: #1847d4;
  --dark-mode-black: #121212;
  --dark-mode-white: #c9d1d9;
  --default-accent-color-dark: #0c2d6b;
  --default-border-dark: #575757;
  --default-border-light: #aeaeae;
  --default-tinted: rgba(128, 128, 144, 0.125);
  --green-100: #26aa55;
  --green-300: #7cef99;
  --green-400: #67c87f;
  --green-500: #34dc70;
  --grey-200: #e1e4e8;
  --grey-40: #666;
  --grey-500: #828282;
  --grey-900: #191919;
  --nice-grey: #f8f8f8;
  --red-500: #d73a49;
  --white: #fff;
  --yellow-300: #ffea7f;
  /* ICONS -- see assets/img/README.md for sources and license details */
  /* Are you a designer? New icon PRs welcomed! */
  --image-alt-speed-disabled: url('../img/modern-tortoise.png');
  --image-alt-speed-enabled: url('../img/modern-tortoise-blue.png');
  --image-baseline: url('../img/horizontal-rule.svg');
  --image-chevron-dn: url('../img/chevron-down.svg');
  --image-chevron-up: url('../img/chevron-up.svg');
  --image-lock-fill: url('../img/lock-fill.svg');
  --image-magnet: url('../img/magnet.svg');
  --image-pause-circle-active: url('../img/pause-circle-active.svg');
  --image-pause-circle-idle: url('../img/pause-circle-idle.svg');
  --image-play-circle-active: url('../img/play-circle-active.svg');
  --image-play-circle-idle: url('../img/play-circle-idle.svg');
  --app-image-url: url('../img/box.svg');
  --audio-image-url: url('../img/music.svg');
  --folder-image-url: url('../img/folder.svg');
  --font-image-url: url('../img/type.svg');
  --image-image-url: url('../img/image.svg');
  --package-image-url: url('../img/package.svg');
  --text-image-url: url('../img/file-text.svg');
  --video-image-url: url('../img/film.svg');
  --progress: 100%;
  --color-bg-even: var(--default-tinted);
  --color-bg-tabs: var(--default-tinted);
  --color-fg-error: var(--red-500);
  --color-fg-port-closed: var(--red-500);
  --color-fg-port-open: var(--green-100);
  --color-progressbar-background-2: var(--green-400);
  --color-progressbar-leech: var(--blue-200);
  --color-progressbar-magnet: var(--yellow-300);
  --color-progressbar-paused: var(--grey-200);
  --color-progressbar-queued: var(--blue-400);
  --color-progressbar-verify: var(--yellow-300);
  --color-toolbar-background: var(--default-tinted);
  color-scheme: light dark;
}
@media (prefers-color-scheme: dark) {
  :root {
    --color-bg-hover: var(--grey-40);
    --color-bg-odd: var(--black);
    --color-bg-popup: var(--black);
    --color-bg-primary: var(--black);
    --color-bg-selected: var(--default-accent-color-dark);
    --color-bg-warn: #cf6679;
    --color-border-default: var(--default-border-dark);
    --color-border-stark: var(--dark-mode-white);
    --color-border-starkest: var(--grey-500);
    --color-fg-disabled: #808088;
    --color-fg-on-popup: var(--nice-grey);
    --color-fg-primary: var(--dark-mode-white);
    --color-fg-secondary: var(--nice-grey);
    --color-fg-selected: var(--dark-mode-white);
    --color-fg-tertiary: var(--grey-500);
    --color-fg-warn: var(--dark-mode-black);
    --color-progressbar-paused: var(--grey-500);
    --color-progressbar-seed-1: var(--green-100);
    --color-progressbar-seed-2: var(--green-400);
    --color-progressbar-seed-paused: var(--grey-500);
  }
  :root .contrast-more {
    --color-bg-even: var(--black);
    --color-bg-hover: var(--grey-40);
    --color-bg-selected: var(--blue-300);
    --color-bg-tabs: var(--black);
    --color-bg-warn: #cf6679;
    --color-border-default: var(--white);
    --color-border-stark: var(--white);
    --color-border-starkest: var(--white);
    --color-fg-disabled: var(--white);
    --color-fg-error: var(--red-500);
    --color-fg-on-popup: var(--white);
    --color-fg-port-closed: var(--red-500);
    --color-fg-port-open: var(--green-100);
    --color-fg-primary: var(--white);
    --color-fg-secondary: var(--white);
    --color-fg-selected: var(--white);
    --color-fg-tabs: var(--white);
    --color-fg-tertiary: var(--white);
    --color-fg-warn: var(--black);
    --color-progressbar-background-2: var(--white);
    --color-progressbar-magnet: var(--yellow-300);
    --color-progressbar-paused: var(--grey-500);
    --color-progressbar-queued: var(--blue-400);
    --color-progressbar-seed-1: var(--black);
    --color-progressbar-seed-2: var(--white);
    --color-progressbar-seed-paused: var(--grey-500);
    --color-progressbar-verify: var(--yellow-300);
    --color-toolbar-background: var(--black);
  }
}
@media (prefers-color-scheme: light) {
  :root {
    --color-bg-hover: var(--nice-grey);
    --color-bg-odd: var(--white);
    --color-bg-popup: var(--white);
    --color-bg-primary: var(--white);
    --color-bg-selected: var(--blue-300);
    --color-bg-warn: #e4606d5b;
    --color-border-default: var(--default-border-light);
    --color-border-stark: var(--grey-500);
    --color-border-starkest: #d0d7de;
    --color-dialog-border: var(--nice-grey);
    --color-fg-disabled: var(--grey-500);
    --color-fg-error: var(--red-500);
    --color-fg-on-popup: var(--grey-900);
    --color-fg-port-closed: var(--red-500);
    --color-fg-port-open: var(--green-400);
    --color-fg-primary: #404040;
    --color-fg-secondary: var(--grey-500);
    --color-fg-selected: var(--nice-grey);
    --color-fg-tertiary: var(--grey-500);
    --color-fg-warn: #cf212e;
    --color-progressbar-leech: var(--blue-100);
    --color-progressbar-magnet: var(--yellow-300);
    --color-progressbar-paused: var(--grey-200);
    --color-progressbar-queued: var(--blue-400);
    --color-progressbar-seed-1: var(--green-500);
    --color-progressbar-seed-2: var(--green-300);
    --color-progressbar-seed-paused: var(--grey-200);
    --color-progressbar-verify: var(--yellow-300);
  }
  :root .contrast-more {
    --color-bg-even: var(--white);
    --color-bg-hover: var(--grey-40);
    --color-bg-selected: var(--blue-300);
    --color-bg-tabs: var(--white);
    --color-bg-warn: #cf6679;
    --color-border-default: var(--black);
    --color-border-stark: var(--black);
    --color-border-starkest: var(--black);
    --color-fg-disabled: var(--black);
    --color-fg-error: var(--red-500);
    --color-fg-on-popup: var(--black);
    --color-fg-port-closed: var(--red-500);
    --color-fg-port-open: var(--green-400);
    --color-fg-primary: var(--black);
    --color-fg-secondary: var(--black);
    --color-fg-selected: var(--white);
    --color-fg-tabs: var(--black);
    --color-fg-tertiary: var(--black);
    --color-fg-warn: var(--white);
    --color-progressbar-background-2: var(--white);
    --color-progressbar-leech: var(--blue-200);
    --color-progressbar-magnet: var(--yellow-300);
    --color-progressbar-paused: var(--grey-500);
    --color-progressbar-queued: var(--blue-400);
    --color-progressbar-seed-1: var(--black);
    --color-progressbar-seed-2: var(--white);
    --color-progressbar-seed-paused: var(--grey-500);
    --color-progressbar-verify: var(--yellow-300);
    --color-toolbar-background: var(--white);
  }
}

/* If browser can support using 'system color' keywords (i.e. way to inherit browser colors), use these variables instead */
@supports (background-color: Canvas) {
  :root .contrast-less {
    --color-bg-odd: Canvas;
    --color-bg-primary: Canvas;
    --color-bg-popup: Canvas;
  }
}

html, body {
  background-color: var(--color-bg-primary);
  color: var(--color-fg-primary);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  height: 100%;
  margin: 0;
}

img {
  border: 0;
}

a {
  color: var(--color-fg-primary);
  outline: 0;
  text-decoration: underline;
}

.hidden {
  display: none;
}

#mainwin {
  display: flex;
  flex-direction: column;
  height: 100%;
  position: relative;
}
#mainwin > * {
  margin: 0;
  padding: 0;
}
#mainwin .flexible-space {
  margin-right: auto;
}

#mainwin-workarea {
  display: flex;
  flex: 1;
  flex-direction: row;
  overflow: auto;
}

.popup, .tabs-container {
  box-shadow: 0 3px 6px -4px rgba(0, 0, 0, 0.12), 0 6px 16px rgba(0, 0, 0, 0.08), 0 9px 28px 8px rgba(0, 0, 0, 0.06);
  position: absolute;
}

#mainwin-toolbar {
  align-items: center;
  background: var(--color-toolbar-background);
  border-bottom: 1px solid var(--color-border-default);
  display: flex;
  flex-direction: row;
  height: var(--toolbar-height);
  margin: 0;
  width: 100%;
}
#mainwin-toolbar button {
  background-color: transparent;
  background-position: center;
  background-repeat: no-repeat;
  border: 0;
  border-radius: 5px;
  cursor: pointer;
  height: var(--toolbar-height);
  margin-right: 6px;
  user-select: none;
  -webkit-user-select: none;
  width: var(--toolbar-height);
}
#mainwin-toolbar button svg {
  stroke: var(--color-fg-primary);
}
#mainwin-toolbar button svg g path {
  fill: var(--color-fg-primary);
}
#mainwin-toolbar button:disabled {
  cursor: default;
}
#mainwin-toolbar button:disabled svg {
  stroke: var(--color-fg-disabled);
}
#mainwin-toolbar button:disabled svg g path {
  fill: var(--color-fg-disabled);
}
#mainwin-toolbar > .toolbar-separator {
  border-left: 1px solid var(--color-border-default);
  height: 25px;
  margin: 0 6px 0 0;
}
#mainwin-toolbar :nth-last-child(2) {
  border: 0;
  flex-grow: 1;
}

.toolbar-icon {
  stroke: var(--color-fg-primary);
}

#toolbar-overflow {
  margin-right: 4px;
}
#toolbar-overflow svg {
  fill: var(--color-fg-primary);
  height: 36px;
  width: 36px;
}
#toolbar-overflow.alt-speed-enabled {
  background-image: var(--image-alt-speed-enabled);
  background-position: bottom left;
  background-size: 20px;
}

#mainwin-statusbar {
  white-space: nowrap;
  align-items: center;
  background: var(--color-toolbar-background);
  border-bottom: 1px solid var(--color-border-default);
  display: flex;
  flex-direction: row;
  height: 30px;
  padding-left: 5px;
}
@media (width < 600px) {
  #mainwin-statusbar :not(select):not(input):not(option):not(.flexible-space) {
    display: none;
  }
}
#mainwin-statusbar #torrent-search {
  width: 100%;
  max-width: 160px;
}
#mainwin-statusbar select {
  width: 70px;
}
#mainwin-statusbar > * {
  margin-right: 5px;
}

#speed-up-icon, #speed-dn-icon {
  fill: var(--color-fg-primary);
}
#speed-up-icon svg, #speed-dn-icon svg {
  width: 20px;
}

#speed-dn-label, #speed-up-label {
  text-align: right;
}

#torrent-container {
  flex-grow: 1;
  left: 0;
  margin: 0;
  overflow: auto;
  padding: 0;
  right: 0;
}

#torrent-list {
  cursor: pointer;
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align: left;
  width: 100%;
  user-select: none;
  -webkit-user-select: none;
}
#torrent-list .torrent {
  border-bottom: 1px solid var(--color-border-default);
}
#torrent-list .torrent:nth-child(even) {
  background-color: var(--color-bg-even);
}
#torrent-list .torrent:nth-child(odd) {
  background-color: var(--color-bg-odd);
}
#torrent-list .torrent .icon {
  background-color: var(--color-fg-primary);
  background-position: center, top left;
  -webkit-mask-image: var(--mime-icon-url);
  -webkit-mask-repeat: no-repeat;
  mask-image: var(--mime-icon-url);
  mask-repeat: no-repeat;
}
#torrent-list .torrent .icon[data-icon-multifile=true] {
  -webkit-mask-image: var(--mime-icon-url), var(--folder-image-url);
  -webkit-mask-position: top left, bottom right;
  -webkit-mask-repeat: no-repeat, no-repeat;
  -webkit-mask-size: 16px, 16px;
  mask-image: var(--mime-icon-url), var(--folder-image-url);
  mask-position: top left, bottom right;
  mask-repeat: no-repeat, no-repeat;
  mask-size: 16px, 16px;
}
#torrent-list .torrent .icon[data-icon-mime-type=audio] {
  --mime-icon-url: var(--audio-image-url);
}
#torrent-list .torrent .icon[data-icon-mime-type=application] {
  --mime-icon-url: var(--app-image-url);
}
#torrent-list .torrent .icon[data-icon-mime-type=font] {
  --mime-icon-url: var(--font-image-url);
}
#torrent-list .torrent .icon[data-icon-mime-type=image] {
  --mime-icon-url: var(--image-image-url);
}
#torrent-list .torrent .icon[data-icon-mime-type=model] {
  --mime-icon-url: var(--package-image-url);
}
#torrent-list .torrent .icon[data-icon-mime-type=text] {
  --mime-icon-url: var(--text-image-url);
}
#torrent-list .torrent .icon[data-icon-mime-type=video] {
  --mime-icon-url: var(--video-image-url);
}
#torrent-list .torrent .torrent-name {
  font-weight: bold;
  margin-bottom: 2px;
  margin-top: 2px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
#torrent-list .torrent .torrent-name.compact {
  font-size: 1em;
  font-weight: normal;
}
#torrent-list .torrent .torrent-labels {
  font-size: small;
  font-weight: normal;
  margin-bottom: 2px;
  margin-top: 2px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 1px 0;
}
#torrent-list .torrent .torrent-labels.compact {
  flex: 1;
  font-size: small;
}
#torrent-list .torrent .torrent-label {
  padding: 0 8px;
  margin: 0 2px;
  border-radius: 1em;
  border: 1px solid var(--color-border-default);
}
#torrent-list .torrent .torrent-progress-details.error, #torrent-list .torrent .torrent-peer-details.error {
  color: var(--color-fg-error);
}
#torrent-list .torrent .torrent-progress-details, #torrent-list .torrent .torrent-peer-details {
  font-size: small;
}
#torrent-list .torrent.compact {
  --icon-size: 16px;
  align-items: center;
  display: flex;
  flex-direction: row-reverse;
}
#torrent-list .torrent.compact .icon {
  -webkit-mask-size: var(--icon-size), calc(var(--icon-size) / 2);
  flex-shrink: 0;
  height: var(--icon-size);
  mask-size: var(--icon-size), calc(var(--icon-size) / 2);
  width: var(--icon-size);
}
#torrent-list .torrent.compact .icon[data-icon-multifile=true] {
  --mime-icon-url: var(--folder-image-url);
}
#torrent-list .torrent.compact > * {
  margin: 5px;
}
#torrent-list .torrent:not(.compact) {
  --icon-size: 32px;
  align-items: center;
  display: grid;
  grid-column-gap: 12px;
  grid-template-areas: "icon name labels" "icon progress-text progress-text" "icon progressbar progressbar" "icon peers peers";
  grid-template-columns: var(--icon-size) auto 1fr;
  padding: 6px 12px;
}
#torrent-list .torrent:not(.compact) .icon {
  background-size: var(--icon-size), calc(var(--icon-size) / 2);
  grid-area: icon;
  height: var(--icon-size);
  width: var(--icon-size);
}
#torrent-list .torrent:not(.compact) .torrent-name {
  grid-area: name;
}
#torrent-list .torrent:not(.compact) .torrent-labels {
  grid-area: labels;
}
#torrent-list .torrent:not(.compact) .torrent-peer-details {
  grid-area: peers;
}
#torrent-list .torrent:not(.compact) .torrent-progress {
  display: flex;
  flex-direction: row;
  grid-area: progressbar;
}
#torrent-list .torrent:not(.compact) .torrent-progress-details {
  grid-area: progress-text;
}
#torrent-list .torrent:not(.compact) > * {
  margin: 1px 0;
}
#torrent-list .torrent.paused {
  color: var(--color-fg-disabled);
}
#torrent-list .torrent.paused .icon {
  background-color: var(--color-fg-disabled);
}
#torrent-list .torrent.selected {
  background-color: var(--color-bg-selected);
  color: var(--color-fg-selected);
}
#torrent-list .torrent.selected .icon {
  background-color: var(--color-fg-selected);
}
#torrent-list .icon {
  background-position: center;
  background-repeat: no-repeat;
}
#torrent-list .torrent-pauseresume-button {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: var(--pauseresume-size);
  margin-left: calc(var(--pauseresume-size) / 2);
  width: var(--pauseresume-size);
}
#torrent-list .torrent-pauseresume-button[data-action=pause] {
  background-image: var(--image-pause-circle-idle);
}
#torrent-list .torrent-pauseresume-button[data-action=pause]:active, #torrent-list .torrent-pauseresume-button[data-action=pause]:hover {
  background-image: var(--image-pause-circle-active);
}
#torrent-list .torrent-pauseresume-button[data-action=resume] {
  background-image: var(--image-play-circle-idle);
}
#torrent-list .torrent-pauseresume-button[data-action=resume]:active, #torrent-list .torrent-pauseresume-button[data-action=resume]:hover {
  background-image: var(--image-play-circle-active);
}
#torrent-list .torrent-progress-bar {
  background-repeat: no-repeat;
  border-color: var(--color-border-starkest);
  border-radius: 3px;
  border-style: solid;
  border-width: 1px;
  height: calc(var(--progressbar-height) - var(--progressbar-border) * 2);
}
#torrent-list .torrent-progress-bar.full {
  flex-grow: 1;
  margin: 2px 0;
}
#torrent-list .torrent-progress-bar.compact {
  min-width: 100px;
  width: 10vw;
}
#torrent-list .torrent-progress-bar.leech {
  background: linear-gradient(to right, var(--color-progressbar-leech) 0, var(--color-progressbar-leech) var(--progress, 30%), transparent var(--progress, 30%)), no-repeat;
}
#torrent-list .torrent-progress-bar.leech.queued {
  background: linear-gradient(to right, var(--color-progressbar-queued) 0, var(--color-progressbar-queued) var(--progress, 30%), transparent var(--progress, 30%)), no-repeat;
}
#torrent-list .torrent-progress-bar.magnet {
  background: linear-gradient(to right, var(--color-progressbar-magnet) 0, var(--color-progressbar-magnet) var(--progress, 30%), transparent var(--progress, 30%)), no-repeat;
}
#torrent-list .torrent-progress-bar.seed.paused {
  background-color: var(--color-progressbar-seed-paused);
}
#torrent-list .torrent-progress-bar.seed:not(.paused) {
  background: linear-gradient(to right, var(--color-progressbar-seed-1) 0, var(--color-progressbar-seed-1) var(--progress, 30%), var(--color-progressbar-seed-2) var(--progress, 30%)), no-repeat;
}
#torrent-list .torrent-progress-bar.seed.queued {
  background-color: var(--color-progressbar-seed-paused);
}
#torrent-list .torrent-progress-bar.verify {
  background: linear-gradient(to right, var(--color-progressbar-verify) 0, var(--color-progressbar-verify) var(--progress, 30%), transparent var(--progress, 30%)), no-repeat;
}
#torrent-list .torrent-progress-bar.paused {
  background: linear-gradient(to right, var(--color-progressbar-paused) 0, var(--color-progressbar-paused) var(--progress, 30%), transparent var(--progress, 30%)), no-repeat;
}

@media (width < 600px) {
  input[type=checkbox] {
    height: 20px;
    width: 20px;
  }
}

@media (width < 600px) {
  #inspector, #prefs-dialog {
    left: 0;
  }
}
@media (width >= 600px) {
  #inspector, #prefs-dialog {
    border-left: 1px solid var(--color-border-default);
    width: 570px;
  }
}

#prefs-dialog {
  background: var(--color-bg-primary);
  bottom: 0;
  right: 0;
  top: var(--popup-top);
  z-index: var(--z-index-popup);
}
#prefs-dialog .tabs-page {
  grid-column-gap: 8px;
  grid-row-gap: 5px;
  grid-template-columns: 1fr 1fr;
  margin: 20px;
}
#prefs-dialog .tabs-page:not(.hidden) {
  display: grid;
}
#prefs-dialog .tabs-page .section-label {
  font-weight: bold;
  grid-column: span 2;
  padding-bottom: 5px;
}
#prefs-dialog .tabs-page .section-label:not(:first-of-type) {
  margin-top: 20px;
}
#prefs-dialog .alt-speed-section-label {
  background: transparent var(--image-alt-speed-enabled) no-repeat;
  background-position: left;
  background-position-y: 4px;
  background-size: 20px;
  padding-left: 22px;
}
#prefs-dialog :disabled, #prefs-dialog .disabled {
  color: var(--color-fg-disabled);
}
